<template>
    <v-container>
        <v-file-input
        accept=".sqlite3"
        hide-input
        prepend-icon="mdi-database"
        @change="import_database"
        ></v-file-input>
        <WarriorsBank :warriors_data="warrior_data" :linked_database='has_database' @add_warrior_card="add_warrior"></WarriorsBank>
        <br>
        <v-btn
        color=primary
        dark
        :disabled="!has_database"
        @click="download_database"
        >Save Out Database</v-btn>
    </v-container>
</template>

<script>
import WarriorsBank from './WarriorsBank';
var initSqlJs = require('sql.js');
var SQL;
var db = null;
export default {
    name: "CardDatabase",
    components: {
        WarriorsBank,
    },
    data: function () {
        return {
            warrior_data: [],
            has_database: false
        }
    },
    async created () {
        SQL = await initSqlJs({locateFile: file => `https://sql.js.org/dist/${file}`});
    },
    methods: {
        "import_database": function (file) {
            var self = this;
            var reader = new FileReader();
            reader.onload = function () {
                var Uints = new Uint8Array(reader.result);
                db = new SQL.Database(Uints);
                self.has_database = true;
                //let's do a quick test to check I have access to database
                var stmt = db.prepare("SELECT * FROM warriors");
                while (stmt.step()) {
                    //console.log(stmt.getAsObject());
                    self.warrior_data.push(stmt.getAsObject());
                }
            }
            reader.readAsArrayBuffer(file);
        },
        "add_warrior": function () {
            //insert a new warrior into the warriors table
            if(db !== undefined) {
                var self = this;
                var nextID = self.warrior_data.length+1;
                var sqlstr = "INSERT INTO warriors VALUES ("+nextID+", '', '', '', 0, 0, 0, 1, '');"
                db.run(sqlstr);

                self.warrior_data = [];
                var stmt = db.prepare("SELECT * FROM warriors");
                while (stmt.step()) {
                    self.warrior_data.push(stmt.getAsObject());
                }
            }
        },
        "download_database": function() {
            var binaryArray = db.export();

            var blob=new Blob([binaryArray], {type: "application/sqlite3"});
            var link=document.createElement('a');
            link.href=window.URL.createObjectURL(blob);
            link.download="card_database.sqlite3";
            link.click();
        },
    }
}
</script>