<template>
  <v-container>
    <h1>Warriors Bank</h1>
    <!--<v-file-input
      accept=".csv"
      hide-input
      @change="import_csv"
    ></v-file-input>-->
    <v-row>
      <v-spacer/>
      <v-btn
        color = primary
        @click="$emit('add_warrior_card')"
        dark
        justify ="end"
        :disabled="!linked_database"
      >Add Warrior</v-btn>
    </v-row>
    <br>
    <v-data-table
      :headers="card_headers"
      :items="warriors_data"
      :items-per-page="5"
      class="elevation-1"
      show-expand
    >
      <template v-slot:item.effect="{ item }">
        {{ effect_text(item.id) }}
      </template>
      <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length">
                <v-container>
                    <v-row>
                        <v-text-field
                            v-model=item.name
                            label="Name"
                        ></v-text-field>
                        <v-spacer></v-spacer>
                        <v-select
                            v-model=item.class
                            :items="classes"
                            label="Class"
                        ></v-select>
                        <v-spacer></v-spacer>
                        <v-text-field
                            v-model=item.cost
                            label="Cost"
                        ></v-text-field>
                        <v-spacer></v-spacer>
                        <v-text-field
                            v-model=item.armor
                            label="Armor"
                        ></v-text-field>
                        <v-spacer></v-spacer>
                        <v-text-field
                            v-model=item.health
                            label="Health"
                        ></v-text-field>
                        <v-spacer></v-spacer>
                        <v-text-field
                            v-model=item.attack
                            label="Attack"
                        ></v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field
                            v-text=item.effect
                            label="Effect"
                        ></v-text-field>
                        <v-spacer></v-spacer>
                        <EffectEditor :card_effect="item.effect" :card_id="item.id" @updated_effect="update_effect"></EffectEditor>
                    </v-row>
                </v-container>
            </td>
        </template>
    </v-data-table>
  </v-container>
</template>

<script>
  import EffectEditor from './EffectEditor';
  const Papa = require('papaparse');
  var _ = require('lodash');

  export default {
    name: "WarriorsBank",
    components: {
      EffectEditor,
    },
    props: ["warriors_data", "linked_database"],
    data: function () {
      return {
        card_headers: [
            {text: 'ID', value: 'id', align: 'start'},
            {text: 'Name', value: 'name', align: 'start'},
            {text: 'Class', value: 'class', align: 'start'},
            {text: 'Cost', value: 'cost', align: 'start'},
            {text: 'Armor', value: 'armor', align: 'start'},
            {text: 'Health', value: 'health', align: 'start'},
            {text: 'Attack', value: 'attack', align: 'start'},
            {text: 'Effect', value: 'effect', align: 'start'}
        ],
        classes: ["orc", "elf", "beastfolk", "necro", "grei"],
        cards: []
      }
    },
    created: function () {
        //
    },
    methods: {
        "import_csv": function (file) {
            var self = this;
            Papa.parse(file,{header: true, complete:function(results) { self.parsed_csv(results); } });
        },
        "parsed_csv": function(results) {
            var self = this;
            console.log("Parsing complete:", results);
            //insert them into tests
            self.cards = results.data;
        },
        "effect_text": function (id) {
            var self = this;
            var val = '';
            var index = _.findIndex( self.warriors_data, ['id', id]);
            if (index > -1 && self.warriors_data[index].effect.length > 0) {
                var parsed = JSON.parse(self.warriors_data[index].effect);
                val = parsed[0].text;
            }
            //console.log("Effect for card id [" + id + "]: " + val);
            return val;
        },
        "update_effect": function (effect, id) {
            var self = this;
            console.log("Update Effect");
            var index = _.findIndex( self.warriors_data, ['id', id]);
            self.warriors_data[index].effect = effect;
        }
    }
  };
</script>
