<template>
    <v-dialog
        v-model="dialog"
        persistent
        max-width="70%"
    >
        <template v-slot:activator="{ on, attrs}">
            <v-btn
                color="primary"
                dark
                v-bind="attrs"
                v-on="on"
                @click="get_effect"
            >Edit Effect</v-btn>
        </template>
        <v-card>
            <v-card-title>
                <span class="headline">Effect Editor</span>
            </v-card-title>
            <v-card-text>
                <v-container>
                    <!-- Build the edit portion here -->
                    <!-- For each effect -->
                    <div v-for="effect_item in effect" :key="effect_item.id">
                        <v-row>
                            <h1>Effect Chunk {{effect_item.id}}</h1>
                            <v-spacer/>
                            <v-btn
                                color="red darken-1"
                                dark
                                v-if="effect_item.id > 1"
                                justify ="end"
                                @click="remove_effect_chunk(effect_item.id)"
                            >Delete</v-btn>
                        </v-row>
                        <hr>
                        <br>
                        <!-- Type -->
                            <!-- Focus Dropdown -->
                            <!-- Type Dropdown -->
                            <!-- End Condition Dropdown -->
                        <h2>Type</h2>
                        <v-layout>
                            <v-select
                                v-model="effect_item.type.focus"
                                :items="type.focus"
                                label="Focus"
                            ></v-select>
                            <v-spacer></v-spacer>
                            <v-select
                                v-model="effect_item.type.sub"
                                @click="updateTypeType(effect_item.type.focus)"
                                :items='effect_item.type.focus=="move_card" ? type.move_card_types : type.type'
                                label="SubType"
                            ></v-select>
                            <v-spacer></v-spacer>
                            <v-select
                                v-model="effect_item.type.end"
                                :items="type.end"
                                label="End Condition"
                            ></v-select>
                        </v-layout>
                        <br>
                        <!-- Target -->
                            <!-- Focus Dropdown -->
                            <!-- Number Field -->
                            <!-- Restriction Options -->
                                <!-- Add Restriction Item -->
                        <h2>Target</h2>
                        <v-layout>
                            <v-select
                                v-model="effect_item.target.focus"
                                :items="target.focus"
                                label="Focus"
                            ></v-select>
                            <v-spacer></v-spacer>
                            <v-text-field
                                v-model="effect_item.target.number"
                                label="Number of Targets"
                            ></v-text-field>
                            <v-spacer></v-spacer>
                            <v-select
                                v-model="effect_item.target.restriction"
                                :items="target.restriction"
                                multiple
                                chips
                                label="Restrictions"
                            ></v-select>
                        </v-layout>
                        <br>
                        <!-- Stat -->
                            <!-- Focus Dropdown -->
                            <!-- Type Dropdown -->
                            <!-- Amount Field -->
                        <v-checkbox
                            @click="showStat(effect_item)"
                            label="Has Stat?"
                            v-model="effect_item.has_stat"
                        ></v-checkbox>
                        <div v-if="effect_item.stat!==null">
                            <h2>Stat</h2>
                            <v-layout>
                                <v-select
                                    v-model="effect_item.stat.focus"
                                    :items="stat.focus"
                                    label="Focus"
                                ></v-select>
                                <v-spacer></v-spacer>
                                <v-select
                                    v-model="effect_item.stat.type"
                                    @click="updateStatType(effect_item.stat.focus)"
                                    :items='effect_item.stat.focus=="ability" ? stat.ability_types : effect_item.stat.focus=="status" ? stat.status_types : stat.type'
                                    label="Type"
                                ></v-select>
                                <v-spacer></v-spacer>
                                <v-text-field
                                    v-model="effect_item.stat.amount"
                                    label="Amount to apply"
                                ></v-text-field>
                            </v-layout>
                            <br>
                        </div>
                        <!-- Card Text Field -->
                        <h2>Text</h2>
                        <v-text-field
                            v-model="effect_item.text"
                            label="Effect Text"
                        ></v-text-field>
                        <br>
                    </div>
                    <br>
                    <v-btn
                        color="blue darken-1"
                        dark
                        @click="add_effect_chunk"
                    >Add Effect Chunk</v-btn>
                </v-container>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    color="blue darken-1"
                    text
                    @click="dialog = false"
                >Close</v-btn>
                <v-btn
                    color="blue darken-1"
                    text
                    @click="save_effect"
                >Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
var _ = require('lodash');
export default {
    name: "EffectEditor",
    props: ["card_effect", "card_id"],
    data: function () {
        return {
            dialog: false,
            type: {
                focus: ["modify_stat", "move_card", "counter", "owner", "ability"],
                type: ["instant", "temporary", "repeat", "swap", "change"],
                move_card_types: ["rift_to_hand", "field_to_hand", "hand_to_deck", "deck_to_rift", "rift_to_deck", "hand_to_field", "deck_to_field"],
                end: ["null", "after_use", "after_turn", "after_battle", "leave_field",]
            },
            target: {
                focus: ["card_on_field", "played_card", "card_in_rift", "card_in_deck", "card_in_hand"],
                restriction: ["warrior", "effect", "targets_self", "targets_others", "self", "others",  "orc", "elf", "necro", "grei"]
            },
            stat: {
                focus: ["attack", "health", "armor", "cost", "ability", "ap", "status"],
                type: ["increase", "decrease", "boost", "hinder"],
                ability_types: ["direct", "guardian", "multi_attack", "wall", "taunt", "revenge"],
                status_types: ["regen", "poison", "stun"]
            },
            lastTypeFocus: null,
            lastStatFocus: null,
            effect: null
        };
    },
    methods: {
        "get_effect": function () {
            var self = this;
            if (self.card_effect.length > 0) {
                var val = JSON.parse(self.card_effect);
                //for each item set the has_stat value
                var curr_id = 0;
                _.forEach(val, function(item) {
                    item.has_stat = item.stat!=null;
                    item.id = ++curr_id;
                });
                self.effect = val;
            }
            else
            {
                self.effect = [{"type":{"focus":"","sub":"","end":null},"target":{"focus":"","number":"","restriction":[]},"stat":null,"text":"","id":1}];
            }
        },
        "add_effect_chunk": function () {
            var self = this;
            self.effect.push({"type":{"focus":"","sub":"","end":null},"target":{"focus":"","number":"","restriction":[]},"stat":null,"text":"", "id":(self.effect.length+1)});
        },
        "remove_effect_chunk": function (id) {
            var self = this;
            var index = _.findIndex(self.effect, ['id',id]);
            self.effect.splice(index, 1);
            //reassign IDs
            var curr_id = 0;
            _.forEach(self.effect, function(item) {
                item.id = ++ curr_id;
            });
        },
        "save_effect": function () {
            var self = this;
            _.forEach(self.effect, function(item) {
                delete item.has_stat;
                delete item.id;
            });
            console.log("Effect saved!");
            console.log(self.effect);
            self.$emit("updated_effect", JSON.stringify(self.effect), self.card_id);
            self.dialog = false;
        },
        "showStat": function (item) {
            var self = this;
            item.stat!=null? item.stat=null : item.stat=[];
            self.$forceUpdate();
        },
        "updateTypeType": function(focus) {
            var self = this;
            if(focus != self.lastTypeFocus && ((self.lastTypeFocus || focus)=="move_card"))
            {
                console.log("Refreshed");
                self.lastTypeFocus = focus;
                self.$forceUpdate();
            }
        },
        "updateStatType": function(focus) {
            var self = this;
            if(focus != self.lastStatFocus && ((self.lastStatFocus || focus)==("ability"||"status")))
            {
                console.log("Refreshed");
                self.lastStatFocus = focus;
                self.$forceUpdate();
            }
        }
    }
};
</script>